export const getEncuestaByClub = (club,token) => {

    return fetch(`https://api.klubo.club/api/club/encuesta/${club}`, {
          method: "GET",
          headers: {
              Accept: 'application/json',
              "Content-type": "application/json",
              Authorization: `Bearer ${token}`
          },
      })
          .then(response => {
              return response.json()
          })
          .catch(err => console.log(err))
  
  }

